.planet-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.7);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.planet-modal {
  background: #18181b;
  border-radius: 1.5rem;
  padding: 2.5rem 2.5rem;
  box-shadow: 0 8px 32px #000a;
  max-width: 700px;
  min-width: 520px;
  min-height: 520px;
  text-align: left;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2.5rem;
}

.planet-modal-close {
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background: linear-gradient(90deg, #22223b 0%, #4a4e69 100%);
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  border: none;
  box-shadow: 0 0 12px 2px #4a4e6955;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10001;
  padding: 0;
}

.planet-modal-tabs {
  position: absolute;
  top: -3.2rem;
  left: 2.5rem;
  display: flex;
  gap: 0.5rem;
  background: #18181b;
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  box-shadow: 0 4px 16px 0 #0004;
  padding: 0.15rem 0.4rem 0 0.4rem;
  z-index: 10002;
}

.planet-modal-nav {
  position: absolute;
  top: 50%;
  z-index: 10003;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  pointer-events: auto;
  transform: translateY(-50%);
}

.planet-modal-nav-left {
  left: -3.7rem;
}
.planet-modal-nav-right {
  right: -3.7rem;
}

.planet-modal-content-left {
  flex: 0 0 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 420px;
}

.planet-modal-content-right {
  flex: 1 1 0;
  min-width: 0;
  position: relative;
  min-height: 420px;
}

.planet-modal-title {
  color: #ffe680;
  font-size: 2.1rem;
  font-weight: 700;
  margin-bottom: 0.8rem;
  text-align: center;
}

.planet-modal-img {
  width: 220px;
  height: 220px;
  object-fit: contain;
  display: block;
  margin-bottom: 0.8rem;
  cursor: zoom-in;
  border-radius: 1rem;
  background: #23223b;
  border: 3px solid #ffe680;
  box-shadow: 0 0 16px 4px #ffe68055;
  padding: 0.5rem;
}

.planet-modal-surface-img {
  width: 320px;
  height: 320px;
  object-fit: contain;
  display: block;
  margin-bottom: 0.8rem;
  border-radius: 1rem;
  background: #23223b;
  border: 3px solid #7f00ff;
  box-shadow: 0 0 16px 4px #7f00ff55;
  padding: 0.5rem;
  cursor: zoom-in;
}

.planet-modal-surface-title {
  color: #ffe680;
  font-weight: 700;
  font-size: 1.35rem;
  margin-bottom: 0.7rem;
  text-align: left;
  letter-spacing: 0.01em;
}

.planet-modal-surface-details {
  color: #fff;
  font-weight: 500;
  font-size: 1.1rem;
  margin-bottom: 1.2rem;
  text-align: left;
}

.planet-modal-surface-temps {
  display: flex;
  justify-content: center;
  gap: 1.2rem;
  margin-bottom: 1.5rem;
}

.planet-modal-surface-temp-cool {
  color: #60a5fa;
  font-weight: 600;
  font-size: 1.05rem;
}
.planet-modal-surface-temp-hot {
  color: #f87171;
  font-weight: 600;
  font-size: 1.05rem;
}

.planet-modal-read-btns {
  display: flex;
  gap: 1rem;
  justify-content: flex-start;
}

.planet-modal-tab-info {
    background: transparent;
    color: #ffe680;
    font-weight: 700;
    font-size: 1.1rem;
    border-radius: 1.5rem 1.5rem 0 0;
    border: none;
    padding: 0.7rem 2.2rem;
    cursor: pointer;
    box-shadow: none;
    outline: none;
    transition: 'background' 0.2s, color 0.2s;
    border-bottom: 4px solid transparent;
    margin-bottom: 2px;
}

.planet-modal-tab-info.active {
    background: linear-gradient(90deg, #ffa751 0%, #ffe680 100%);
    color: #22223b;
    box-shadow: 0 0 8px 2px #ffe68055;
    outline: 2px solid #ffe680;
    border-bottom: 4px solid #ffa751;
}

.planet-modal-tab-surface {
    background: transparent;
    color: #7f00ff;
    font-weight: 700;
    font-size: 1.1rem;
    border-radius: 1.5rem 1.5rem 0 0;
    border: none;
    padding: 0.7rem 2.2rem;
    cursor: pointer;
    box-shadow: none;
    outline: none;
    transition: 'background' 0.2s, color 0.2s;
    border-bottom: 4px solid transparent;
    margin-bottom: 2px;
}

.planet-modal-tab-surface.active {
   background: linear-gradient(90deg, #7f00ff 0%, #3a3aff 100%);
   color: #fff;
   box-shadow: 0 0 8px 2px #7f00ff55;
   outline: 2px solid #7f00ff;
   border-bottom: 4px solid #3a3aff;
}

.planet-modal-navigation-arrow-container {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10003;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    pointer-events: auto;
}
.planet-modal-navigation-arrow-container.left {
    left: -3.7rem;
}

.planet-modal-navigation-arrow-container.right {
    right: -3.7rem;
}

.planet-modal-navigation-arrow-container-button {
    background: linear-gradient(90deg, rgb(34, 34, 59) 0%, rgb(74, 78, 105) 100%);
    color: white;
    font-size: 2rem;
    font-weight: 700;
    width: 2.5rem;
    height: 2.5rem;
    line-height: 1;
    padding: 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 9999px;
    box-shadow: 0 8px 32px 0 rgba(0,0,0,0.18), 0 1.5px 6px 0 #ffe68055;
    cursor: pointer;
    overflow: visible;
    opacity: 1;
    outline: none;
    transition: 'background' 0.2s, color 0.2s;
}

.planet-modal-navigation-arrow {
    display: block;
    width: 100%;
    text-align: center;
    z-index: 2;
    position: relative;
}

.zoom-ring {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 3px solid #ffe680;
    opacity: 0;
    transition: opacity 0.2s;
    pointer-events: none;
}